import React, { Component } from 'react';
import {Line} from 'react-chartjs-2';
import {checkStatus, parseJSON} from "./util";

//https://wikimedia.org/api/rest_v1/metrics/pageviews/per-article/en.wikipedia/all-access/user/Cat/daily/2018112500/2018121500

const formatDate = (d) => {
  const dateStr = d.getFullYear() + ("0"+(d.getMonth()+1)).slice(-2) + ("0" + d.getDate()).slice(-2) + "00";
  //("0" + d.getDate()).slice(-2) + "-" + ("0"+(d.getMonth()+1)).slice(-2) + "-" +    d.getFullYear() + " " + ("0" + d.getHours()).slice(-2) + ":" + ("0" + d.getMinutes()).slice(-2);
  //console.log(dateStr);

  return dateStr;
};

class Pageviews extends Component {
  constructor(props) {
    super(props);
    this.state = {
      preview: 'Uzgaidi brdi, kamr tiek ieldti dati...',
      chartData: {},
      loaded: false,
      error: false
    };
  }
  
  componentDidMount() {
    if (this.props.title !== "") {
      this.show(this.props);
    } else {
      this.setState({preview: "Netika nordts lapas nosaukums",loaded: true});
    }
}

setPreviewHtml(value) {
  const rawdata = value.items;
  
  let views = rawdata.map(data => data.views);
  let dates = rawdata.map(data => data.timestamp.slice(0, -2));
  
const data = {
  labels: dates,
  datasets: [
    {
      label: 'Lapas skatjumi',
      fill: false,
      backgroundColor: 'rgba(75,192,192,0.4)',
      borderColor: 'rgba(75,192,192,1)',
      data: views
    }
  ]
};
  
    this.setState({chartData: data,loaded: true});
}

show(props) {
  let yesterday = ( d => new Date(d.setDate(d.getDate()-1)) )(new Date);
  let firstDay = ( d => new Date(d.setDate(d.getDate()-90)) )(new Date);
  
  const previewUrl = '//wikimedia.org/api/rest_v1/metrics/pageviews/per-article/'+props.wiki+'.wikipedia/all-access/user/'+encodeURIComponent(props.title)+'/daily/'+formatDate(firstDay)+'/'+formatDate(yesterday);

  //console.log(previewUrl);

      //console.log(previewUrl);
  fetch(previewUrl)
      .then(checkStatus)
      .then(parseJSON)
      //.then(this.validateParseResponse.bind(this))
      //.then(this.buildPreviewHtml.bind(this))
      .then(this.setPreviewHtml.bind(this))
      .catch(this.setPreviewFailed.bind(this));
}

setPreviewFailed() {
  this.setState({preview: "Notika kda, ieldjot lapas skatjumu datus", error: true});
}

  render() {
    const {preview, error, chartData} = this.state;

    return (
      <div style={{width: '45%'}}>
      <h4>Lapas skatjumi {this.props.wiki}wiki</h4>
        {error ? <div>{preview}</div> : <div>
        <Line data={chartData} height={150}
   /></div>}
      </div>
    );
  }
}

export default Pageviews;


