//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import {checkStatus, encodeParams, parseJSON} from "./util";
//import './Preview.css';
import Pageviews from './Pageviews';

class Preview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      image: null,
      previewHtml: 'Uzgaidi brdi, kamr tiek ieldts lapas saturs...'
    };
  }
  
  componentDidMount() {
    if (this.props.title !== "") {
      this.show(this.props);
    } else {
      this.setState({previewHtml: "Netika nordts lapas nosaukums"});
    }
}

show(props) {
    const previewUrl = 'https://'+props.wiki+'.wikipedia.org/w/api.php?' + encodeParams({
            action: 'query',
            format: 'json',
            origin: '*',
            disableeditsection: 0,
            prop: 'extracts|pageimages',
			  	  redirects: 1,
            titles: props.title,
            exintro: 1,
            explaintext: 1,
            piprop: "thumbnail",
            pithumbsize: "104"
        });
        
        console.log(previewUrl);
    fetch(previewUrl)
        .then(checkStatus)
        .then(parseJSON)
        //.then(this.validateParseResponse.bind(this))
        //.then(this.buildPreviewHtml.bind(this))
        .then(this.setPreviewHtml.bind(this))
        .catch(this.setPreviewFailed.bind(this));
}

validateParseResponse(data) {
    if (!data.hasOwnProperty('parse')
        || !data['parse'].hasOwnProperty('text')
        || !data['parse']['text'].hasOwnProperty('*')
        || !data['parse'].hasOwnProperty('title')) {
        return Promise.reject.bind(Promise);
    } else {
        return data;
    }
}

setPreviewHtml(value) {
  var thispageapires = value['query']['pages'];
  var page_api_result = thispageapires[Object.keys(thispageapires)[0]];
  var imageURL = (page_api_result.thumbnail ? page_api_result.thumbnail.source : null);

    this.setState({previewHtml: page_api_result.extract, image: imageURL});
}

setPreviewFailed() {
    this.setState({previewHtml: "Notika kda, ieldjot lapas saturu"});
}

  render() {
    return (
      <div>
        <h4>Ievads {this.props.wiki}wiki</h4>
        {this.state.image ? <img alt="" src={this.state.image} width="150" align="right" style={{padding: '10px'}} /> : ""}
        <br />
        <div>{this.state.previewHtml}</div>
        <br />
        <Pageviews wiki={this.props.wiki} title={this.props.title} />
      </div>
    );
  }
}

export default Preview;


