import React, { Component } from 'react';
import ReactTable from "react-table";
import 'react-table/react-table.css';
import Preview from './Preview';
import apiEndpoint from '../config';

class WpLink extends Component {
  render() {
    const { project, label } = this.props;
	
	return <GeneralLink linkStart={"https://"+project+".wikipedia.org/wiki/"} query={replaceAll(label,' ', "_")} label={label}/>
  }
}

const replaceAll = (str, find, replace) => { return str.replace(new RegExp(find, 'g'), replace); }

//
class GeneralLink extends Component {

  render() {
    const { linkStart, query,label } = this.props;
	const urlEncoded = encodeURIComponent(query);
	const text = replaceAll(label,'_', " ");
	
	return <a target="_blank" href={linkStart + urlEncoded}>{text}</a>
  }
}

class Lvwiki extends Component {
  render() {
    const { article, original, language } = this.props;
	
	if (article==="") {
		return <span><WpLink label={original} project={language}/> (nav iztulkots; <GeneralLink linkStart={"https://www.google.com/search?q="} query={original} label={"Google"}/>)</span>
	} else {
		const lvwikiarticles = article.split("|")
          .map((t,index) => <WpLink key={index} label={t} project={"lv"}/>)
          .reduce((prev, curr) => [prev, ', ', curr])
		  
		return <span>{lvwikiarticles} (<GeneralLink linkStart={"https://www.google.com/search?q="} query={original} label={"Google"}/>)</span>
	}
  }
}

const columns = [
  {
    Header: "Latviski",
    accessor: d => <Lvwiki key={d.wd} article={d.lv} original={d.orig} language={d.lang}/>,
    id: "lv",
    style: { whiteSpace: 'normal' }
  },
  {
    Header: "Cit valod",
    accessor: d => <WpLink label={d.orig} project={d.lang}/>,
    id: "orig",
    style: { whiteSpace: 'normal' }
  },
  {
    Header: "Apraksts",
    accessor: d => d.descr,
    id: "desc",
    width: 600,
    style: { whiteSpace: 'normal' }
  },
  {
    Header: "iw",
    accessor: d => d.iws,
    id: "iws",
    width: 75
  },
  {
    Header: "Vikidati",
    accessor: d => <GeneralLink linkStart={"https://www.wikidata.org/wiki/"} query={d.wd} label={d.wd}/>,
    id: "wd",
    width: 125
  }
];

class Table extends Component {
  constructor() {
    super();
    this.state = {
      data: [],
	  isLoaded: false,
    error: false,
    full: false,
    origFilter: '',
    descFilter: '',
    lastUpdated: ''
    };
    this.handleChange = this.handleChange.bind(this);
    this.onFull = this.onFull.bind(this);
  }

  loadData() {
    let isFull = "";

    if (this.state.full) {
      isFull = "?full=1";
    }

      fetch(apiEndpoint+isFull)
        .then(res => res.json() )
        .then(
          (result) => {
            //console.log(result);
        this.setState({
              isLoaded: true,
              data: result['articles'],
              lastUpdated: result['time']
            });
          },
          (error) => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        )
    }
  
    onFull() {
      if (!this.state.full) {
        this.setState({full: true}, () => this.loadData())
      }
    }
  
  componentDidMount() {
    this.loadData();
  }
    handleChange(event) {
      const target = event.target;
      const value = target.type === 'checkbox' ? target.checked : target.value;
      const name = target.name;
  
      this.setState({
        [name]: value
      });
    }
  
  render() {
	const { error, isLoaded, data, lastUpdated } = this.state;
    let whatToRender;
	
	if (error) {
      whatToRender = <div>Nevarja ieldt saturu, kdas paziojums: {error.message}</div>;
    } else if (!isLoaded) {
      whatToRender = <div className="alert alert-warning" role="alert">  Uzgaidi brdi, tiek ieldti dati!</div>;
    } else {
      whatToRender = <div>
        <div className="form-horizontal">
  <div className="form-group">
    <label className="col-md-2 control-label">Filtrt oriinlwiki nosaukumu</label>
    <div className="col-md-3">
    <input type="text" className="form-control" name="origFilter" value={this.state.origFilter} onChange={this.handleChange} />
    </div>
  </div>
  <div className="form-group">
    <label className="col-md-2 control-label">Filtrt aprakstu</label>
    <div className="col-md-3">
    <input type="text" className="form-control" name="descFilter" value={this.state.descFilter} onChange={this.handleChange} />
    </div>
  </div>
  <button type="button" className="btn btn-primary" onClick={this.onFull}>Ieldt visu tabulu</button>
  </div>
  <br />
  <span>Dati pdjoreiz atjauninti: {lastUpdated}</span>
  <br />
  <br />
  <ReactTable
          data={data
            .filter(obj => this.state.origFilter === '' || obj.orig.toLowerCase().includes(this.state.origFilter.toLowerCase()))
            .filter(obj => this.state.descFilter === '' || obj.descr.toLowerCase().includes(this.state.descFilter.toLowerCase()))
          }
          columns={columns}
		  pageSizeOptions= {[50, 100, 500, 1000]}
          defaultPageSize={50}
          showPagination={true}
          minRows={0}
          SubComponent={row => {
            //console.log('fsdfdf');
            return (
              <div style={{ padding: "20px" }}>
                <Preview wiki={row.original.lang} title={row.original.orig} />
              </div>
            );
          }}
              
		  defaultSorted={[
            {
              id: "iw",
              desc: false
            }
          ]}
          className="-striped -highlight"
        /></div>
    }
	
	return (
      <div>
		{whatToRender}
      </div>
    );
  }
}

export default Table;


